/* Header */
body{
    background: #EFEFEF;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.header{
    background: $azul;
    padding: 1rem 0;
    z-index: 9;
}

.menu-movil{
    text-align: center;
    color: $blanco;
    font-size: 2.5rem;
    @include tablet{
        display: none;
    }
}

.menuM{
    opacity: 0;
    transition: all ease 0.5s;
}

.navegacion{
    width: 90%;
    max-height: 15rem;
    margin: 0 auto;

    @include tablet{
        display: grid;
        grid-template-columns: 20% 80%;
        gap: 2rem;
        max-height: 7rem;
    }
    .logo{
        display: block;
        width: 8rem;
        margin: 0 auto;
        @include tablet{
            width: 8rem;
        }   
    }
}

.nave-items{
    background: $azul;
    display: block;
    @include tablet{
        padding-top: 3rem;
        display: flex;
        justify-content: space-between;
    }
    a{
        padding: 0.5rem 1rem;
        display: block;
        text-align: center;
        text-decoration: none;
        color: $blanco;
        font-size: 2rem;
        &:hover{
            border-bottom: 0.2rem solid $blanco;
        }
    }
    .items{
        display: none;
        position: absolute;
        background: lighten($color: $azul, $amount: 5);
        border: 0.2rem solid darken($color: #0c5192, $amount: 5);
        li{
            padding: 0.5rem 5rem;
            @include tablet{padding: 0.5rem 0;}
            &:hover{
                background: lighten($color: $azul, $amount: 10);

            }
        }
    
    }
    &:hover .items{display: block;}
}
