/* Glider Catalogo */
.catalogo{
    margin: -2rem auto 3rem auto;
}

.gccat{
    margin: 1rem auto;
    height: 6rem;
}

.gbC{
    margin: 1rem auto;
    height: 6rem;
}

.gc{}

.item-gliderc{
    
    width: 100%;
    a{
        p{
            text-align: center;
            text-transform: uppercase;
            padding: 1rem 2rem;
            background: $blanco;
            font-weight: $bold;
            border-radius: 1rem;
        }
        &:hover{
            background: darken($color: $blanco, $amount: 20);
        }
    }
}
