/* Nosotros */
.nosotrosBaner{
    padding: 15rem 1rem;
    margin: 0.5rem auto;
    border-radius: 1rem;
    background-image: linear-gradient(120deg, rgba(161, 196, 253, 0.3) 0%, rgba(194, 233, 251, 0.3) 100%), url('../img/nosotros.webp');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    box-shadow: 0.4rem 0.4rem 1rem #CCCCCC;
}

.nosotros{
    padding: 2rem;
    margin: 1rem auto 2rem auto;
    background: $blanco;
    p{
        text-align: justify;
        line-height: 1.4;
    }
    @include tablet{
        padding: 2rem 3rem;
        picture{
            width: 40%;
            margin: 0 auto;
        }
    }
}

