/* Footer */
.footer{
    background: #6e6d6d;
    padding: 1rem 0;;
}

.contenido-footer{
    
    .logo{
        width: 50%;
        margin: 0 auto;
        padding: 1rem 0;
    }
    .slogan{
        margin: 0 auto;
        padding: 3rem 0;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        h4{
            text-align: center;
            color: #FFFFFF;
        }
        p{
            text-align: center;
            color: #FFFFFF;
        }
        .sloganFace{
            width: 100%;
        }
    }

    .fcontacto{
        width: 100%;
        margin: 0 auto;
        h4{
            text-align: center;
            color: #FFFFFF;
        }
        p{
            margin: 2rem auto;
            text-align: center;
            a{
                color: #FFFFFF;
                i{
                    background-color: #FFFFFF;
                    border-radius: 2rem;
                    font-size: 3rem;
                    padding: 0.5rem;
                    color: #10B418;
                    &:hover{
                        background-color: #10B418;
                        color: #FFFFFF;
                    }
                }
            }
        }
    }

    @include tablet{
        @include grid(3, 2rem);
    }
}

.derechos{
    font-size: 1.5rem;
    padding: 0.5rem 0;
    text-align: center;
    color: #FFFFFF;
    background: #0c5192;
    margin-bottom: -2rem;
}