/* Categorias */
.categorias{
    
    @include tablet{
        @include grid(2, 2rem);
    }
}

.categoria{
    background: $blanco;
    padding: 2rem 1rem;
    margin: 1rem auto;
    @include tablet{
        height: 70rem;
    }
}

.productos{
    @include grid(2, 1rem);
    border: 0.1rem solid darken($color: $blanco, $amount: 15);
    margin-top: 0.5rem;
    height: 40rem;
}

.producto{
    padding: 2rem 1rem;
    position: relative;
    picture img{
        position: relative;
        margin: auto;
        height: 16rem;
        @include tablet{
            height: 16rem;
        }
    }

    .pDescripcion{
        padding: 1rem;
        width: 100%;
        height: 16rem;
        position: absolute;
        top: 2rem;
        left: 0;
        background: rgba(12, 81, 146, 0.7);
        border-radius: 1rem;
        h4, p{
            text-align: center;
            color: $blanco;
        }
        h4{
            font-size: 2rem;
        }
        p{
            line-height: 1.3;
            font-size: 1.4rem;
        }
        opacity: 0;
        transition: all ease 0.3s;

        @include tablet{
            h4{font-size: 2.2rem;}
            p{font-size: 1.6rem;}
        }
    }
    &:hover .pDescripcion{
        opacity: 1;
    }

    @include tablet{
        height: 14rem;
    }
}

.descripcion{
    padding: 1rem;
    border: 0.1rem solid darken($color: $blanco, $amount: 15);
    h3{
        font-size: 2rem;
        margin-top: -1rem;
        text-transform: uppercase;
        @include tablet{
            font-size: 3rem;
        }
    }
    p{
        text-align: justify;
        line-height: 1.2;
        font-size: 1.8rem;
    }
}