/* Glider */

.carrito{
    margin: 2rem auto;
    width: 80%;
    @include tablet{
        width: 80%;
    }
}

.gradient-border-bottom{}

.gradient-border{
    border: 0.2rem solid darken($color: $azul, $amount: 10);
    border-radius: 1rem;
    box-shadow: 0.5rem 0.5rem 1rem lighten($color: $negro, $amount: 20);
    height: 18rem;
    @include tablet{
        height: 35rem;
    }
}

.glider{}

.item-gliderm{
    height: 20rem;
    background-image: linear-gradient(to top, lighten($color: $azul, $amount: 5) 0%, lighten($color: $azul, $amount: 15) 50%, lighten($color: $azul, $amount: 25) 100%);
    border-radius: 1rem;
    picture{
        padding: 1rem 0;
        width: 33rem;
        height: 100%;
        margin: 0 auto;
        @include tablet{
            width: 69rem;
        }
    }
}

.arrow{
    font-size: 2rem;
    padding: 0.5rem 0.8rem;
    border-radius: 50%;
    background: $negro;
    @include tablet{
        font-size: 3rem;
        padding: 1rem 1.5rem;
    }
}
.arrow-left{}
.arrow-right{}